<template>
  <!-- 
    https://www.dowebok.com/188.html
  liMarquee  jQuery  HTML  marquee  marquee  Web  xml -->
  <div>
    <!-- {{topPos}} -->
    <!-- {{leftPos}}/{{strMoveLeft}} -->
    <div
      class="dowebok str_wrap"
      ref="dowebok"
      :style="'height: '+height+'px;'"
      @mouseenter="mouseenterHandle"
      @mouseleave="mouseleaveHanlde"
    >
      <div
        class="str_move str_origin"
        ref="strmove"
        :style="{'left': leftPos +'px', 'top': topPos+ 'px'}"
      >
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "cq-liMarquee",
  props: {
    direction: {
      type: String,
      default: "left" //  (left | right | up | down)
    },
    loop: {
      type: Number,
      default: -1 // -1 
    },
    scrolldelay: {
      type: Number,
      default: 0 // ()
    },
    scrollamount: {
      type: Number,
      default: 50 // 
    },
    circular: {
      type: Boolean,
      default: true //  false marquee 
    },
    runshort: {
      type: Boolean,
      default: true // 
    },
    hoverstop: {
      type: Boolean,
      default: true // 
    },
    inverthover: {
      type: Boolean,
      default: false // 
    }
  },
  data() {
    return {
      topPos: 0,
      leftPos: 0,
      dowebok: null,
      height: 0,
      strMoveLeft: 0,
      strMoveTop: 0,
      currentLoop: 0,
      timer: null,
      str_move_clone: null
    };
  },
  created() {},
  mounted() {
    let dowebok = this.$refs.dowebok,
      str_move = this.$refs.strmove;
    if (this.direction === "top" || this.direction === "bottom") {
      dowebok.classList.add("str_vertical");
    }
    this.$nextTick(() => {
      setTimeout(() => {
        this.strMoveLeft = str_move.clientWidth;
        this.strMoveTop = str_move.clientHeight;
      }, 30);
    });

    let str_move_clone;
    // copy
    str_move_clone = str_move.cloneNode(true);
    if (this.direction === "top" || this.direction === "bottom") {
      str_move_clone.style.top = str_move.clientHeight + "px";
    } else {
      str_move_clone.style.left = str_move.clientWidth + "px";
    }
    dowebok.append(str_move_clone);
    this.str_move_clone = str_move_clone;

    this.$nextTick(() => {
      setTimeout(() => {
        this.height = str_move.clientHeight;
        if (!this.runshort) {
          if (dowebok.clientWidth > str_move.clientWidth) return;
        }

        if (!this.inverthover) {
          this.move();
        }
      }, 40);
    });
  },
  methods: {
    move() {
      let {
        loop,
        leftPos,
        topPos,
        strMoveLeft,
        strMoveTop,
        scrollamount
      } = this;

      if (this.direction === "top" || this.direction === "bottom") {
        if (Math.abs(topPos) < strMoveTop) {
          switch (this.direction) {
            case "top":
              this.topPos -= 1;
              break;
            case "bottom":
              this.topPos += 1;
              break;
            default:
              break;
          }
        } else {
          this.topPos += strMoveTop;
        }
        this.str_move_clone.style.top = this.height + this.topPos + "px";
      } else {
        if (Math.abs(leftPos) < strMoveLeft) {
          switch (this.direction) {
            case "left":
              this.leftPos -= 1;
              break;
            case "right":
              this.leftPos += 1;
              break;
            default:
              break;
          }
        } else {
          this.leftPos += strMoveLeft;
        }
        this.str_move_clone.style.left = this.strMoveLeft + this.leftPos + "px";
      }

      //  / 
      this.timer = setTimeout(this.move, strMoveLeft / scrollamount);
    },
    pause() {},
    play() {},
    destroy() {},
    update() {},
    mouseenterHandle() {
      let { hoverstop, inverthover, strMoveLeft, scrollamount, timer } = this;
      if (!hoverstop) return;
      hoverstop && !inverthover ? clearTimeout(timer) : this.move();
    },
    mouseleaveHanlde() {
      let { hoverstop, inverthover, strMoveLeft, scrollamount, timer } = this;
      if (!hoverstop) return;
      hoverstop && !inverthover ? this.move() : clearTimeout(timer);
    }
  }
};
</script>

<style lang="less">
@import "./assets/css/liMarquee.less";
</style>